@media screen and (max-width: 1023px) {
  h1 {
    font-size: 47px !important;
    line-height: 50px !important;
  }

  h2 {
    font-size: 33px !important;
    line-height: 40px !important;
  }

  .main .main-section__block2_item p {
    font-size: 16px !important;
    line-height: 22px !important;
  }
  .main .main-section__block2_item p:first-of-type {
    font-size: 80px !important;
    line-height: 50px !important;
  }
  .main .main-section__block2_item p:first-of-type span {
    font-size: 22px !important;
    line-height: 30px !important;
  }
}
/**
  Мобильные устройства
 */
@media screen and (min-width: 767px) {
  .header .burger-icon {
    display: none;
  }
  .header__block {
    display: flex;
  }
}
@media screen and (max-width: 767px) {
  h1 {
    font-size: 33px !important;
    line-height: 38px !important;
    max-width: 80%;
  }

  .header .burger-active {
    display: flex !important;
  }
  .header .burger-icon {
    display: block;
    width: 40px;
    height: 40px;
  }
  .header .burger-icon:hover {
    cursor: pointer;
    transform: scale(1.2);
  }
  .header__block {
    display: none;
    flex-direction: column;
    justify-content: space-between;
    align-items: end;
    height: 100vh;
    background-color: #f3c3c3;
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
  }
  .header__block nav {
    display: flex;
    flex-direction: column;
    align-items: end;
    margin: 0 !important;
    background-color: #D83838;
    width: 50%;
    height: 100vh;
  }
  .header__block nav .close-icon {
    display: block !important;
    width: 41px;
    height: 41px;
    margin: 10px;
  }
  .header__block nav .close-icon:hover {
    cursor: pointer;
    transform: scale(1.2);
  }
  .header__block nav div {
    display: flex;
    flex-direction: column;
    align-items: end;
    margin-right: 40px;
  }
  .header__block nav a {
    color: #FFFFFF !important;
    margin-bottom: 10px;
    font-weight: 700;
  }
  .header__block_contacts {
    background-color: #D83838;
    width: 50%;
    margin-top: 0 !important;
    padding: 10px;
  }
  .header__block_contacts svg {
    fill: white;
  }
  .header__block_contacts a {
    color: #FFFFFF !important;
  }

  .main .main-section {
    margin: 0 auto;
  }
  .main .main-section__block1 {
    margin-bottom: 80px !important;
  }
  .main .main-section__block1 p {
    font-size: 16px !important;
    line-height: 22px !important;
  }
  .main .main-section__block2 {
    display: grid !important;
    grid-template: 1fr 1fr/1fr 1fr;
    gap: 38px;
  }
  .main .main-section__block2_item {
    margin: 0 auto;
  }
}
@media screen and (max-width: 576px) {
  h1 {
    text-align: center;
    max-width: 300px !important;
    margin: 0 auto 10px auto !important;
  }

  .btn {
    display: flex !important;
    margin: 0 auto 20px auto !important;
  }

  .header {
    margin-bottom: 30px !important;
  }

  .main .wave-img {
    top: 300px !important;
    left: 0px !important;
    z-index: -5;
  }
  .main .main-section__block1 p {
    text-align: center;
    max-width: 300px;
    margin: 0 auto 30px auto !important;
  }
  .main .main-section__block1 span {
    display: block !important;
    margin: 0 auto;
    text-align: center;
    max-width: 300px;
  }
  .main .main-section__block2 {
    margin-bottom: 52px !important;
  }
  .main .main-section__block3 {
    flex-direction: column-reverse;
  }
  .main .main-section__block3_img {
    width: 100%;
    min-height: 300px !important;
  }
  .main .main-section__block3 p {
    font-size: 16px !important;
    line-height: 20px !important;
    margin-bottom: 13px;
  }
}
body {
  margin: 0 auto;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #000000;
}

.container {
  max-width: 1268px;
  padding: 0 20px;
  margin: 0 auto;
}

h1 {
  font-weight: 700;
  font-size: 64px;
  line-height: 100px;
  text-transform: uppercase;
}

h2 {
  font-weight: 700;
  font-size: 48px;
  line-height: 59px;
  text-transform: uppercase;
}

.red {
  color: #D83838;
}

.btn {
  width: 261px;
  height: 70px;
  background-color: #D83838;
  color: #FFFFFF;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
}
.btn:hover {
  transform: scale(1.1);
}

.header {
  margin-bottom: 90px;
}
.header .container {
  display: flex;
  justify-content: space-between;
  padding-top: 38px;
  font-weight: 500;
}
.header__logo {
  display: flex;
}
.header__logo svg {
  width: 24px;
  height: 58px;
  margin-right: 14px;
}
.header__logo p {
  font-family: "PT Serif";
  font-weight: 700;
  font-size: 22px;
  line-height: 25px;
  text-transform: uppercase;
}
.header__block nav {
  margin: 8px 80px 0 0;
}
.header__block nav .close-icon {
  display: none;
}
.header__block nav a {
  display: inline-block;
  margin-left: 16px;
  text-decoration: none;
  color: #000000;
}
.header__block nav a:hover {
  transform: scale(1.2);
}
.header__block_contacts {
  display: flex;
  justify-content: flex-end;
  margin-top: 8px;
}
.header__block_contacts svg {
  width: 22px;
  height: 22px;
}
.header__block_contacts > svg {
  width: 14px;
  height: 14px;
  margin: 4px 4px 0 0;
}
.header__block_contacts .phones {
  display: flex;
  flex-direction: column;
}
.header__block_contacts .phones a {
  color: #000000;
  border-bottom: 1px solid #D83838;
  text-decoration: none;
}
.header__block_contacts .social {
  display: flex;
  justify-content: flex-end;
}
.header__block_contacts .social a {
  border-bottom: unset;
  margin: 4px 0 0 6px;
}

.main .wave-img {
  position: absolute;
  top: 40px;
  left: 43%;
  max-width: 100%;
  object-fit: cover;
}
.main .main-section__block1 {
  margin-bottom: 230px;
}
.main .main-section__block1 h1 {
  margin-bottom: 30px;
}
.main .main-section__block1 p {
  font-weight: 400;
  font-size: 22px;
  line-height: 27px;
}
.main .main-section__block1 a {
  margin: 46px 20px 0 0;
}
.main .main-section__block1_span {
  display: inline-block;
  max-width: 276px;
  vertical-align: middle;
}
.main .main-section__block2 {
  display: flex;
  justify-content: space-between;
  margin-bottom: 90px;
}
.main .main-section__block2_item {
  max-width: 210px;
}
.main .main-section__block2_item p {
  font-weight: 500;
  font-size: 22px;
  line-height: 30px;
  text-align: center;
}
.main .main-section__block2_item p:first-of-type {
  font-weight: 700;
  font-size: 95px;
  line-height: 49px;
  text-transform: uppercase;
  border-bottom: 3px solid #B7B7B7;
  padding-bottom: 30px;
  margin-bottom: 10px;
}
.main .main-section__block2_item p:first-of-type span {
  font-size: 36px;
  line-height: 30px;
  text-transform: lowercase;
  vertical-align: middle;
}
.main .main-section__block3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.main .main-section__block3_img {
  position: relative;
  flex: 0 0 45%;
  min-height: 450px;
}
.main .main-section__block3_img img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  position: absolute;
  top: 0;
  left: 0;
}
.main .main-section__block3 h2 {
  margin-bottom: 22px;
}
.main .main-section__block3 svg {
  width: 54.5px;
  height: 12.5px;
  display: inline-flex;
  float: left;
  margin: 10px 10px 0 0;
}
.main .main-section__block3 p {
  overflow: hidden;
  max-width: 551px;
  font-size: 22px;
  line-height: 27px;
}